import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class GetAllProductService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {

    }
    
    getAllMedicineName(ChemistId,ProductType)
    {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getAllProductsByChemistId?ChemistId="+ChemistId+"&ProductType="+ProductType, options)
            .map(res => res.json());

    }
 
    GetPharmacyNameService(ChemistId,ProductType)
    {
       
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getAllProductsByChemistId?ChemistId="+ChemistId+"&ProductType="+ProductType, options)
            .map(res => res.json());
    
    
        
    }

   
}


