import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class AddDiscountOfferService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {
     
    }
    AddDiscount(DiscountType,CreatedByUserId,ChemistId,StartTimeinMilliSec,EndTimeinMilliSec,DiscountPercentage,title)
    {
        let body = JSON.stringify({ DiscountType:DiscountType,CreatedByUserId: CreatedByUserId,StartTime:StartTimeinMilliSec, EndTime: EndTimeinMilliSec,ChemistId: ChemistId,Title: title});
        console.log(body);
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addDiscountOffer", body, options)
            .map(res => res.json());
    }
    getDiscountOfferService(ChemistId)
    {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getDiscountOffersByChemistId?ChemistId="+ChemistId, options)
            .map(res => res.json());
    }
    AddCustomDiscountService(DiscountOfferId, AddCustomDiscountProductValuesListModelArray)
    {
        let body = JSON.stringify({DiscountOfferId:DiscountOfferId, AddCustomDiscountProductValuesList:AddCustomDiscountProductValuesListModelArray});
        console.log(body);
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addCustomDiscountProducts", body, options)
            .map(res => res.json());
    }
  
   
}









