import { Component, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { AddAreaService } from './../services/AddAreaService';
import { AddCategoryService } from './../services/AddCategoryService';
import { GetChemistDataService } from './../services/GetChemistDataService';
import { AddchemistService } from './../services/AddchemistService';
import { GetChemistDataModel } from './../models/GetChemistDataModel';
import { GetAllCitiesModel } from './../models/GetAllCitiesModel';
import { categoryModel } from './../models/categoryModel';
import { chemistModel } from './../models/chemistModel';
import { GetAreaNameModel } from './../models/GetAreaNameModel';
import { AddDiscountOfferService } from './../services/AddDiscountOfferService';
import { DiscountOfferModel } from './../models/DiscountOfferModel';
import { GetAllProductService } from './../services/GetAllProductService';
import { GetAllProductModel } from './../models/GetAllProductModel';
import { AddCustomDiscountProductValuesListModel } from './../models/AddCustomDiscountProductValuesListModel';

declare var jQuery: any;
@Component({
  selector: 'AddDiscountOffer',
  providers: [AddDiscountOfferService, GetAllProductService],
  styles: [require('./AddDiscountOffer.style.scss'), require('../forms/elements/elements.style.scss')],
  template: require('./AddDiscountOffer.template.html'),
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'chemist-page app'
  },
})

export class AddDiscountOfferComponent {
  router: Router;

  public CreatedByUserId: any;
  public CreatedOnUTC: any;
  public StartTime: any;
  public EnterTime: any;
  public EndTime: any;
  public Title: any;
  public DiscountType: any;
  public ChemistId: any;
  public AddCustomDiscountProductValuesListModelSingle: AddCustomDiscountProductValuesListModel;
  public AddCustomDiscountProductValuesListModelArray: AddCustomDiscountProductValuesListModel[] = [];
  public DiscountPercentage: any;
  public StartTimeinMilliSec: any;
  public EndTimeinMilliSec: any;
  public StatusValue: any;
  public isBoolean: any;
  public AllProduct: boolean;
  public CustomProduct: boolean;
  public DiscountOfferModelArray: DiscountOfferModel[] = [];
  public GetAllProductModelArraySingle: GetAllProductModel[] = [];
  public DiscountOfferId: any;
  public ProductType: any;
  public Value: any;
  public CustomValue: any;
  public UserType:any;
  


  constructor(public _GetProductService: GetAllProductService, public _AddDiscountOfferService: AddDiscountOfferService, router: Router) {
    this.router = router;
    this.ChemistId = localStorage.getItem("GetChemistId");
console.log('chemist id',this.ChemistId)
this.UserType=localStorage.getItem("UserType");
if(this.UserType==null)
{
  this.router.navigate(["/app/login"]);

}
else{

    this._AddDiscountOfferService.getDiscountOfferService(this.ChemistId).subscribe(
      response => {
        console.log(response);
        this.DiscountOfferModelArray = response.data;
        jQuery("#snackbar1").html(response.message);
        this.myFunction();
      });
      console.log("discountoffer",this.DiscountOfferModelArray);
    this.GetProductName();
    }
  }
  GetDiscountCustomValue(Custom_Value) {
    this.CustomValue = Custom_Value;
    console.log(this.CustomValue);
  }
  GetDiscountValue(Value) {
    this.DiscountType = Value;

    if (this.DiscountType == 100) {
      this.AllProduct = true;
      this.CustomProduct = false;
    }
    else {
      this.CustomProduct = true;
      this.AllProduct = false;
    }
  }
  GetProductName() {
    this.ProductType = 100;

    this._GetProductService.GetPharmacyNameService(this.ChemistId, this.ProductType).subscribe(
      response => {
        console.log(response.data);

        this.GetAllProductModelArraySingle = response.data;
        console.log('chemistName', this.GetAllProductModelArraySingle);

      });
  }
  GetDiscountOfferId(OfferId) {
    this.DiscountOfferId = OfferId;
  }
  addList() {
    this.AddCustomDiscountProductValuesListModelSingle = new AddCustomDiscountProductValuesListModel();
    this.AddCustomDiscountProductValuesListModelSingle.ProductId = this.DiscountOfferId;
    this.AddCustomDiscountProductValuesListModelSingle.DiscountValue = this.Value;
    this.AddCustomDiscountProductValuesListModelArray.push(this.AddCustomDiscountProductValuesListModelSingle);
    console.log('custom off all value', this.AddCustomDiscountProductValuesListModelArray);
  }
  addOffer() {
    this.ChemistId = localStorage.getItem("GetChemistId");
    console.log(this.ChemistId);
    if (this.DiscountType == 100) {
      this.CreatedByUserId = localStorage.getItem("UserId");
      console.log(this.CreatedByUserId);
      this.ChemistId = localStorage.getItem("GetChemistId");
      console.log(this.ChemistId);
      this.StartTimeinMilliSec = Math.floor(this.StartTime);
      console.log(this.StartTimeinMilliSec);
      this.EndTimeinMilliSec = Math.floor(this.EndTime);
      console.log(this.EndTimeinMilliSec);
      this._AddDiscountOfferService.AddDiscount(this.DiscountType, this.CreatedByUserId, this.ChemistId, this.StartTimeinMilliSec, this.EndTimeinMilliSec, this.DiscountPercentage, this.Title).subscribe(
        response => {
          jQuery("#snackbar1").html(response.message);
          this.myFunction();
        });
      this.Title = undefined;
      this.DiscountType = undefined;
      this.StartTimeinMilliSec = undefined;
      this.EndTimeinMilliSec = undefined;
    }
    else {

      this._AddDiscountOfferService.AddCustomDiscountService(this.CustomValue, this.AddCustomDiscountProductValuesListModelArray).subscribe(
        response => {
          console.log(response);
          jQuery("#snackbar1").html(response.message);
          this.myFunction();
        });
      this.Value = undefined;
    }
  }
  myFunction() {

    var x = document.getElementById("snackbar1")
    x.className = "show";
    setTimeout(function () { x.className = x.className.replace("show", ""); }, 3000);
  }

}





